const statusCodes = {
  EMPTY: '',
  INCORRECT: 'Incorrect. Try again.',
  CORRECT: 'Correct!! Press enter for next character'
}

class SpaghettiMonolithComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      prompt: '',
      content: '',
      big5Key: '',
      displayUrl: '',
      textAreaEditable: true,
      status: statusCodes.EMPTY,
    };
  }

  setNextPrompt = () => {
    const randomIndex = Math.floor(Math.random() * massiveList.length) + 1;
    const randomChar = massiveList[randomIndex];

    if (randomChar) {
      this.setState({
        prompt: randomChar,
      });
    }
  }

  process = (big5Key) => {
    console.log(big5Key);
    this.setState({
      content: '',
      big5Key: big5Key,
      displayUrl: `http://input.foruto.com/cjdict/Images/CJZD_JPG/${big5Key}.JPG`,
    });
    console.log(this.state.displayUrl);
  };

  setEditable = (value) => {
    if (this.state.textAreaEditable === value) {
      return;
    }

    this.setState({
      textAreaEditable: value
    });
  }

  setStatus = (value) => {
    if (this.state.status === value) {
      return;
    }

    this.setState({
      status: value
    });
  }

  clearContent = () => {
    this.setState({
      content: '',
    });
  }

  makeWhitelistRegex = (ranges) => {
    return new RegExp("[^" + ranges + "]", "g");
  }

  sanitise = (rawContent) => {
    const whitelistRanges = "a-zA-Z0-9\u3400-\u4DB5\u4E00-\u9FCB\uF900-\uFA6A\u3130-\u318F\u1100-\u11FF\uA960-\uA97F\uD7B0-\uD7FF\uAC00-\uD7AF\u3040-\u309F\u30A0-\u30FF\u2E80-\u2FD5";
    const whitelistRegex = this.makeWhitelistRegex(whitelistRanges);
    return rawContent.replace(whitelistRegex, "");
  }

  handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();

      console.log('CONTENTS');
      console.log(this.state.content);

      if (this.state.content === '') {
        // go to next char + reenable textarea
        console.log("NEXT PROMPTTTT");
        this.setStatus(statusCodes.EMPTY);
        this.setNextPrompt();
        this.setEditable(true);
        return;
      }

      if (this.state.content !== this.state.prompt) {
        this.setStatus(statusCodes.INCORRECT);
        console.log('INCORRECT');
        return;
      }
      
      // Otherwise, find image
      this.setStatus(statusCodes.CORRECT);
      console.log("CORRECT!!!!");
      this.clearContent();
      this.setEditable(false);
      console.log(this.state.content);
      const buf = iconv.encode(this.state.content, 'big5');
      let big5Key = '';
      for (const elem of buf) {
        console.log(elem);
        big5Key += elem.toString(16);
      }

      if (big5Key) {
        this.process(big5Key.toUpperCase());
      } else {
        console.warn('No big5 key created, ignoring: ' + content + " " + big5Key);
      }
    }
  }
  
  handleChange = (e) => {
    this.setState({content: this.sanitise(e.target.value)});
  }

  componentDidMount() {
    this.setNextPrompt();
  }

  render() {
    return (
      <>
        <div className='prompt'>{this.state.prompt}</div>
        <div className='status'>{this.state.status}</div>
        <textarea
          className='input'
          id='input'
          maxLength="1"
          onKeyPress={this.handleKeyPress}
          onChange={this.handleChange}
          // disabled={!this.state.textAreaEditable}
        />
        <div>
          <br/>
          {
            this.state.displayUrl
            ? (
                <img src={this.state.displayUrl} />
              )
              : null
          }
        </div>
      </>
    );
  }
}

export default () => <SpaghettiMonolithComponent />;
